.TodoList {
    margin: 4rem auto;
    padding: 2rem 3rem 3rem;
    max-width: 500px;
    background: #ff6666;
    color: #fff;
    box-shadow: -20px -20px 0 0 rgba(100, 100, 100, 0.1);
}
.TodoList ul {
    margin-top: 2.6rem;
    list-style: none;
}
.TodoList h1 {
    font-weight: normal;
    font-style: 2.6rem;
    letter-spacing: .05rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.TodoList h1 span{
    display: block;
    font-size: 0.8rem;
    margin-top: 0.7rem;
    margin-left: 3px;
    margin-top: 0.2rem;
}